.FinishedQuiz {
  padding: 16px;
  color:#000;
  border: 2px solid #000;
  border-radius: 10px;
  box-sizing: border-box;
  margin: 0 10px;
  font-weight: bold;
}

.FinishedQuiz ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.FinishedQuiz ul li i {
  margin-left: 10px;
}
@media (max-width: 575px) {
  .FinishedQuiz {
   width: 280px;
   margin: 5px -15px;
   padding: 5px;
  }
}

